import { createGlobalStyle } from 'styled-components';

const GlobalStyle = createGlobalStyle`
*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    z-index:2;
}
html{
    @media(max-width:900px){
        font-size:80%;
    }
}
body{
    background:#1b1b1b;
    font-family:'gabriola';
}
button{
    font-weight: bold;
    font-size:1.1rem;
    cursor: pointer;
    padding:1rem 2rem;
    border:3px solid #23d997;
    background:transparent;
    color:white;
    transition:all 0.5s ease;
    &:hover{
        background-color:#23d997;
        color:white;
    }
}
h2{
    font-weight:lighter;
    font-size:3rem;
}
h3{
    color:white;
    font-size:1.7rem;
}
h4{
    font-weight:bold;
    color:whitesmoke;
    font-size:2rem;
}
p{
    padding:1.4rem 0rem;
    color:#ccc;
    font-size:1.4rem;
    line-height:160%;    
}
a{
    font-size:1.1rem;
}
span{
    color:#23d997;
    font-weight: bold;
}
`;

export default GlobalStyle;